import React, { useState } from "react";
import {
  Grid,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button,
} from "@mui/material";
import "./service.css";

//  
import s1 from "../images/service/s1.jpeg";
import s2 from "../images/service/s2.jpeg";
import s3 from "../images/service/s3.jpeg";
import s4 from "../images/service/s4.jpeg";
import s5 from "../images/service/s5.jpeg.webp";

const services = [
  {
    title: "   ,    ",
    description:
      "          .",
    image: s1,
    fullDescription:
      "      ,    ,     .          ,         .       ,     .       ,    ,        .    -,          ,    .               .",
  },
  {
    title: " ",
    description:
      "     -  ,      .",
    image: s2,
    fullDescription:
      "            .           ,     .    ,       ,    .           ,        .         -      .",
  },
  {
    title: " ",
    description: " ,     .",
    image: s3,
    fullDescription:
      "              .       ,           .     - ,  , ,         .              .        ,          .",
  },
  {
    title: "  ",
    description:
      "         .",
    image: s5,
    fullDescription:
      "         ,   , ,  ,     -.       ,            .          .          ,    .      ,         .",
  },
  {
    title: " ",
    description:
      "          .",
    image: s4,
    fullDescription:
      "    ,    ,         .      :  ,    ,      .    ,         ,     .",
  },
];

const ServiceSection = () => {
  const [openDialog, setOpenDialog] = useState(false);
  const [selectedService, setSelectedService] = useState(null);

  const handleOpenDialog = (service) => {
    setSelectedService(service);
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setSelectedService(null);
  };

  return (
    <section id="services">
      <Container maxWidth="lg">
        <Typography
          variant="h4"
          gutterBottom
          textAlign="center"
          sx={{ fontWeight: "bold", marginBottom: "2rem", fontSize: "3.5rem" }}
        >
           
        </Typography>

        <Grid container spacing={4} justifyContent="center">
          {services.map((service, index) => (
            <Grid item xs={12} sm={6} md={6} key={index}>
              <div className="card-wrapper">
                <Card
                  className="hover-scale"
                  onClick={() => handleOpenDialog(service)}
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    width: { xs: "100%", md: 500 },
                    height: { md: 340 },
                    borderRadius: "16px",
                    cursor: "pointer",
                  }}
                >
                  <CardMedia
                    component="img"
                    height="200"
                    image={service.image}
                    alt={service.title}
                    sx={{ objectFit: "cover" }}
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography
                      variant="h6"
                      gutterBottom
                      sx={{ fontWeight: "bold" }}
                    >
                      {service.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {service.description}
                    </Typography>
                  </CardContent>
                </Card>
              </div>
            </Grid>
          ))}
        </Grid>
      </Container>

      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        maxWidth="sm"
        fullWidth
        className="custom-dialog"
      >
        <DialogTitle>{selectedService?.title}</DialogTitle>
        <DialogContent>
          <CardMedia
            component="img"
            height="300"
            image={selectedService?.image}
            alt={selectedService?.title}
          />
          <Typography variant="body1" sx={{ marginTop: "1rem" }}>
            {selectedService?.fullDescription}
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}></Button>
        </DialogActions>
      </Dialog>
    </section>
  );
};

export default ServiceSection;
